.video-responsive {
  	margin: 8px;
  	padding-top: 10px;
	overflow: hidden;
	/*box-shadow: 0px 6px 18px -9px rgba(0,0,0,0.75);*/
	transition: transform 100ms ease-in;
	cursor: pointer;
	margin-top: 80px;
	width: auto;
	height: auto;
	text-align: center;

}
.video-responsive > iframe{
	margin-bottom: 20px;
	border-radius: 20px;
	display: block;
	margin-left: auto;
  	margin-right: auto;
  	width: 55%;
  	object-fit: cover;
  	min-width: 100px;
  	min-height: 100px;

}

