body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c33;
  min-height: 8vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

html {
  scroll-behavior: smooth;
}
.navbar{
	position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
}
.BackgroundImage {
  position: relative;
}

h1 {
  text-align: center;
}

.BackgroundImage > img {
  /*Centers the image*/
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  width: 100%;
  /*Centers the image*/
}

.video-responsive {
  	margin: 8px;
  	padding-top: 10px;
	overflow: hidden;
	/*box-shadow: 0px 6px 18px -9px rgba(0,0,0,0.75);*/
	transition: -webkit-transform 100ms ease-in;
	transition: transform 100ms ease-in;
	transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
	cursor: pointer;
	margin-top: 80px;
	width: auto;
	height: auto;
	text-align: center;

}
.video-responsive > iframe{
	margin-bottom: 20px;
	border-radius: 20px;
	display: block;
	margin-left: auto;
  	margin-right: auto;
  	width: 55%;
  	object-fit: cover;
  	min-width: 100px;
  	min-height: 100px;

}


