/* Added s_ before all classNames to resolve conflict arising from matching classNames with ProfileOfficer.css */

.s_profile {
  margin: 10px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 6px 18px -9px rgba(0, 0, 0, 0.75);
  transition: -webkit-transform 100ms ease-in;
  transition: transform 100ms ease-in;
  transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
  cursor: pointer;
  margin-top: 100px;
  width: auto;
  height: auto;
  text-align: center;
}

.s_profile:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

.s_profile > img {
  margin-bottom: 50px;
  border-radius: 12px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 45%;
  object-fit: cover;
  min-width: 100px;
  min-height: 100px;
}

.s_profile {
  margin-top: -9px;
  border-radius: 10px;
  padding: 20px;
  padding-top: 20px;
  border: 1;
}

.s_profile_info > h2 {
  /*background-color: #F8F8F8;*/
  border-radius: 10px;
  font-size: 30px;
  font-weight: 600;
}

.s_profile_info > h4 {
  font-size: 20px;
  font-weight: 400;
  margin-top: 8px;
  margin-bottom: 16px;
}
.s_profile_info > h5 {
  font-size: 14px;
  font-weight: 800;
  margin-top: 8px;
  margin-bottom: 8px;
}
.s_profile_icons {
  padding: 10px;
  display: inline;
}

/* To get the SocialIcons to transform, used ' >* ' to indicate 'all child descendants' of the attribute, but not the attribute itself */
.s_profile_icons > * {
  transition: -webkit-transform 100ms ease-in;
  transition: transform 100ms ease-in;
  transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
}

.s_profile_icons:hover > * {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

